/* login form start */
/* Home */
.home {
  position: relative;
  /* height: 10vh; */
  width: 90%;
  /* background-image: linear-gradient(35deg, black, blue) background-size: cover;
     background-position: center; */
  background: none;
}

.home::before {
  content: "";
  position: absolute;
  height: fit-content;
  /* width: 8%; */
  background-color: white;
  /* border: 5px black solid; */
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  transition: all 0.5s ease-out;
}

.home.show::before {
  opacity: 1;
  pointer-events: auto;
}

/* From */
.form_container {
  position: fixed;
  max-width: 320px;
  width: 90%;
  top: 50%;
  left: 50%;
  height: fit-content;
  transform: translate(-50%, -50%) scale(1.2);
  z-index: 101;
  /* background: #e3e6e3; */
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  padding: 25px;
  border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.1);
  opacity: 0;
  pointer-events: none;
  transition: all 0.6s ease-out;
}

.home.show .form_container {
  opacity: 1;
  pointer-events: auto;
  transform: translate(-50%, -50%) scale(1);
}

.signup_form {
  display: none;
}

.form_container.active .signup_form {
  display: block;
}

.form_container.active .login_form {
  display: none;
}

.form_close {
  position: absolute;
  top: 10px;
  right: 20px;
  color: #0b0217;
  font-size: 22px;
  opacity: 0.7;
  cursor: pointer;
}

.form_container h2 {
  font-size: 22px;
  color: #0b0217;
  text-align: center;
}

.input_box {
  position: relative;
  margin-top: 30px;
  width: 100%;
  height: 40px;
}

.input_box input {
  height: 100%;
  width: 100%;
  border-radius: 30px;
  border: none;
  outline: none;
  padding: 0px 30px;
  color: #333;
  transition: all 0.2s ease;
  border-bottom: 1.5px solid #aaaaaa;
}

.input_box input:focus {
  border-color: #000000;
}

.input_box i {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  color: #707070;
}

.input_box i.email,
.input_box i.password {
  left: 0;
  margin-left: 5px;
}

.input_box input:focus ~ i.email,
.input_box input:focus ~ i.password {
  color: #000000;
}

.input_box i.pw_hide {
  right: 0;
  font-size: 18px;
  cursor: pointer;
  margin-right: 4px;
}

.option_field {
  margin-top: 14px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form_container a {
  color: #7d2ae8;
  font-size: 12px;
}

.form_container a:hover {
  text-decoration: underline;
}

.checkbox {
  display: flex;
  column-gap: 8px;
  white-space: nowrap;
}

.checkbox input {
  accent-color: #abf0cd;
}

.checkbox label {
  font-size: 12px;
  cursor: pointer;
  user-select: none;
  color: #0b0217;
}

.form_container .button {
  background: #91e2acd0;
  margin-top: 30px;
  width: 100%;
  padding: 10px 0;
  border-radius: 10px;
}

.form_container .button:hover {
  background: none;
  border: 1px solid whitesmoke;
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  color: #000;
}

.login_signup {
  font-size: 12px;
  text-align: center;
  margin-top: 15px;
}
/* login form end */

/*--------------------------------------------------------------
# second Footer start
--------------------------------------------------------------*/
#footer {
  color: #37423b;
  font-size: 14px;
  background: #eef0ef;
}

#footer .footer-top {
  padding: 60px 0 30px 0;
  background: #f9faf9;
}

#footer .footer-top .footer-contact {
  margin-bottom: 30px;
}

#footer .footer-top .footer-contact h4 {
  font-size: 22px;
  margin: 0 0 30px 0;
  padding: 2px 0 2px 0;
  line-height: 1;
  font-weight: 700;
}

#footer .footer-top .footer-contact p {
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 0;
  font-family: "Raleway", sans-serif;
  color: #777777;
}

#footer .footer-top h4 {
  font-size: 16px;
  font-weight: bold;
  color: #444444;
  position: relative;
  padding-bottom: 12px;
}

#footer .footer-top .footer-links {
  margin-bottom: 30px;
}

#footer .footer-top .footer-links ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

#footer .footer-top .footer-links ul i {
  padding-right: 2px;
  color: #61b47d;
  font-size: 18px;
  line-height: 1;
}

#footer .footer-top .footer-links ul li {
  padding: 10px 0;
  display: flex;
  align-items: center;
}

#footer .footer-top .footer-links ul li:first-child {
  padding-top: 0;
}

#footer .footer-top .footer-links ul a {
  color: #777777;
  transition: 0.3s;
  display: inline-block;
  line-height: 1;
  text-decoration: none;
}

#footer .footer-top .footer-links ul a:hover {
  text-decoration: none;
  color: #3ac162;
}

#footer .footer-newsletter {
  font-size: 15px;
  /* margin-top: -250px; */
}

#footer .footer-newsletter h4 {
  font-size: 16px;
  font-weight: bold;
  color: #444444;
  position: relative;
  padding-bottom: 12px;
}

#footer .footer-newsletter form {
  margin-top: 30px;
  background: #fff;
  padding: 6px 10px;
  position: relative;
  border-radius: 50px;
  text-align: left;
  border: 1px solid #e0e5e2;
}

#footer .footer-newsletter form input[type="email"] {
  border: 0;
  padding: 4px 8px;
  width: calc(100% - 100px);
}

#footer .footer-newsletter form input[type="submit"] {
  position: absolute;
  top: -1px;
  right: -1px;
  bottom: -1px;
  border: 0;
  background: none;
  font-size: 16px;
  padding: 0 20px 2px 20px;
  background: #50a935;
  color: #fff;
  transition: 0.3s;
  border-radius: 50px;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.1);
}

#footer .footer-newsletter form input[type="submit"]:hover {
  background: #3ac162;
}

#footer .credits {
  padding-top: 3px;
  padding-bottom: 10px;
  font-size: 13px;
  /* border: 1px solid; */
}

#footer .credits a {
  color: #3ac162;
  transition: 0.3s;
  text-decoration: none;
}

#footer .credits a:hover {
  color: #50a935;
}

#footer .social-links a {
  font-size: 18px;
  display: inline-block;
  background: #42db64;
  color: #fff;
  line-height: 1;
  padding: 8px 0;
  /* margin-top: -70%; */
  margin-bottom: 2%;
  margin-right: 4px;
  border-radius: 50%;
  text-align: center;
  /* margin-bottom: 2%; */
  width: 36px;
  height: 36px;
  transition: 0.3s;
}

@media only screen and (max-width: 400px) {
  .social-links {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    /* margin-bottom: 10px; */
  }

  .credits {
    /* margin-bottom: 5%; */
  }
}

#footer .social-links a:hover {
  background: #3ac162;
  color: #fff;
  text-decoration: none;
}

/* second footer end */

/* faculty cards start */
.card-container {
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  height: 369px;
  width: 262.5px;
  overflow: hidden;
  margin: 0 auto;
  border-radius: 20px;
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 1px 15px 0px rgba(181, 235, 214, 0.3);
  background: linear-gradient(125deg, #c9ffe6, #eaddee);
}

.card-container:hover {
  transform: scale(0.98);
}

.card-container .wrapper img {
  position: relative;
  z-index: 20;
  border-radius: 50%;
  display: block;
  height: 130px;
  width: 130px;
  border: 5px solid #fff;
  object-fit: cover;
  margin: 20px auto;
  transition: all 0.3s ease;
}

/* .container:hover .wrapper img.active {
         height: 470px;
         width: 350px;
         margin: 0px auto;
         border: none;
         border-radius: 5px;
      } */

.card-container .wrapper .title {
  color: #494949;
  font-size: 25px;
  font-weight: 500;
  padding: 10px;
  line-height: 25px;
}

.card-container .wrapper .place {
  color: #000;
  font-size: 17px;
  line-height: 0px;
  margin: 10px 0;
}

.card-container .content p {
  color: #000000;
  font-size: 14px;
  margin-top: 30px;
  padding: 1px 20px 10px 20px !important;
}

.card-container .icons {
  position: relative;
  margin-top: -360px;
  margin-left: 3px;
  list-style: none;
}

.card-container .icons.active {
  display: none;
}

.card-container .icons li {
  height: 36px;
  width: 36px;
  margin: 5px 0;
  opacity: 0;
  margin-left: -100px;
  transition: all 0.5s ease;
}

.card-container:hover .icons li {
  opacity: 1;
  margin-left: 7px;
}

.card-container:hover .icons li:nth-child(2) {
  transition-delay: 0.2s;
}

.card-container:hover .icons li:nth-child(3) {
  transition-delay: 0.4s;
}

.card-container .icons li a {
  color: #8c00ff;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  background: #fff;
  display: block;
  line-height: 40px;
  transition: all 0.3s;
}

.card-container .icons li a:hover {
  transform: scale(0.9);
}
/* faculty card end */

